.search {
      -webkit-appearance: none;
      border: 1 px solid black;
      border-radius: 1em;
      outline: none;
      padding: 10px;
      width: 25vw;
      font-family: 'Trade Winds', cursive;
      font-size: larger;
      line-height: 30px;
      margin-bottom: 30px;
      background-color: #46b6d3;
      justify-content: center;
    }
.search::placeholder{
  color: rgb(231, 231, 231);
}
    